h1, h2 {
    color: whitesmoke;
}

h1 {
    font-size: 64px;
}

h2 {
    font-size: 32px;
    margin-bottom: 30%;
}

div {
    width: 100%;
}

section {
    height: 100vh;
    width: 100%;
    background-image: url('../../../../assets/img/bgr1.jpg');
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-size: cover;
    color: white;
    overflow: hidden;
    >div {
        width: 100%;
    }
}

.titel {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
